.plugin{margin:10px;padding:10px;min-width:250px;min-height:250px;background-color:#fff;border-radius:5px;box-shadow:5px 5px 5px #dbdde0;text-align:center;display:flex;flex-direction:column;justify-content:space-between}.plugin:hover{box-shadow:10px 10px 5px #dbdde0;transform:scale(1.1)}.plugin h2{font-weight:normal;font-style:normal;font-size:14pt;text-transform:uppercase}.checkbox-container{height:50px}.checkbox{cursor:pointer;position:relative}.checkbox>input{height:40px;width:50px;position:absolute;left:-25px;top:0rem;-webkit-appearance:none;-moz-appearance:none;-o-appearance:none;appearance:none;border-radius:25px;outline:none;transition-duration:.3s;background-color:#a2f6f2;cursor:pointer}.checkbox>input:checked{background-color:#a2f6f2}.checkbox>input:checked+span::before{font-size:2.4em;content:"âœ“";text-align:center;color:#fff;position:absolute;left:-0.5rem;top:0rem}.checkbox>input:active{border:2px solid #34495e}#icon{margin:10px;margin-top:20px;height:96px;left:50%}